import React from 'react'
import axios from 'axios';

const shortid = require('shortid');

export const sendMessageToChatGPT = async (message,setsohbet,settextkontrol,chatHistory) => {
  settextkontrol(true);

  const messages = [
    { role: 'system', content: 'You are' },
    ...chatHistory, // nceki sohbet gemiini ekleyin
    { role: 'user', content: message }
  ];

  const zaman=new Date()

    try {
      const response = await axios.post('https://api.openai.com/v1/chat/completions', {
        model: 'gpt-3.5-turbo',
        messages: messages
      }, {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': "Bearer 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" 
        }
      }
      );

        setsohbet(old=>[...old,{platform:"ChatGPT",mesaj:response.data.choices[0].message.content,tarih:zaman.getTime()+zaman.getSeconds(1),gonderen:"ai",id:shortid.generate(),durum:"array"}]);
        settextkontrol(false)
 
    
    //  return setgelenmesaj(response.data.choices[0].message.content);
    } catch (error) {
      
      console.error('ChatGPT API hatas:', error);
      // Hata durumunda bo bir yant dndr
      settextkontrol(false);
      return console.error('ChatGPT API hatas:', error), setsohbet(old=>[...old,{platform:"ChatGPT",mesaj:"Bir Hata ile karlald !",tarih:zaman.getTime()+zaman.getSeconds(1),id:shortid.generate(),gonderen:"ai",durum:"array"}]);
      
    }
  };



  